.screen {
   $parent: &;
   position: relative;
   background-color: #edeff1;
   height: 492px;
   &__container {
      position: static;
      display: flex; }
   &__content {
      flex: 0 0 500px;
      padding: 50px 0 65px; }
   &__logo {
      display: block;
      flex: 0 0 198px;
      margin-bottom: 50px; }
   &__title {
      margin-bottom: 40px; }
   &__sub-title {
      max-width: 350px;
      margin-bottom: 50px;
      font-size: 29px; }

   &__link {
      font-size: 17px; }
   &__img-wrap {
      position: absolute;
      top: 0;
      right: 0;
      flex: 0 0 907px; } }
